img {
    width: 100px;
    margin-bottom: 2em;
}

nav {
    font-size: 20px;
    display: flex;
    justify-content: center;
    margin-bottom: 4em;
}

nav a {
    margin-right: 1em;
    text-decoration: none;
    color: #333333;
    display: inline-block;
}

nav a:last-child {
    margin-right: 0;
}

nav a.active{
    font-weight: bold;
}

@media only screen and (min-width: 863px) {
    header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    img {
        margin-bottom: 0;
    }
}
.header {
    width: 100%;
    text-align: center;
}

.sub-header {
    width: 100%;
}

.sub-sub-header {
    width: 100%;
}

p {
    margin: 0;
}
html {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    box-sizing: border-box;
}

*,*::before,*::after {
    box-sizing: border-box;
}

strong, b {
    font-weight: bolder;
}

body {
    background-color: white;
    font-family: 'Roboto', sans-serif;
    color: #333333;
    margin: 1em;
}
